  text-decoration: none;
}
.metroExtraSmall,
.face.metroExtraSmall,
.live-tile .metroExtraSmall {
  font-size: .7em;
  line-height: 1em;
}
a.metroExtraSmall:link,
a.face.metroExtraSmall:link,
a.live-tile .metroExtraSmall:link {
  text-decoration: none;
}
.live-tile.me .metroBig,
.live-tile .bottom {
  display: block;
  position: absolute;
  left: 10px;
  bottom: 10px;
  padding: 0;
  text-decoration: none;
}
.live-tile.half-tile .bottom {
  left: 5px;
}
